            <unicon name="trash" fill="white" width="15" height="15" />
          </b-button>
        </div>

      </template>

      <template v-slot:row-details="row">
        <b-card>
          <b-card-text>
            {{row.item.note}}
          </b-card-text>
        </b-card>
