    fields: [
      { key: 'hero', label: 'Hero' },
      { key: 'placement', label: 'Pos' },
      { key: 'mmr', label: 'MMR' },
      { key: 'tribe', label: 'Tribe' },
      { key: 'summary', label: 'Summary' },
      { key: 'timestamp', label: 'Date' },
      { key: 'actions', label: '' },
